@import 'functions';


@mixin border-gradient($from, $to, $weight: 0) {
  $mix-main: mix($from, $to);
  $mix-sub-from: mix($mix-main, $from);
  $mix-sub-to: mix($mix-main, $to);
  
  box-shadow: inset 0 1px 0 $weight rgba($mix-sub-to, .25),
              inset 0 -1px 0 $weight rgba($mix-sub-from, .25),
              inset 1px 0 0 $weight rgba($mix-sub-to, .25),
              inset -1px 0 0 $weight  rgba($mix-sub-from, .25),
              inset 1px -1px 0 $weight rgba($mix-main, .5),
              inset -1px 1px 0 $weight rgba($mix-main, .5),
              inset 1px 1px 0 $weight rgba($to, .75),
              inset -1px -1px 0 $weight rgba($from, .75);
}


// Responsive
// ==================
@mixin up($size) {
  $size: strip-unit($size);
  @media (min-width: $size * 1px) {
    @content;
  }
}

@mixin down($size) {
    $size: strip-unit($size);
    @media (max-width: $size * 1px) {
      @content;
    }
  }
  @mixin between($down, $up) {
    $down: strip-unit($down);
    $up: strip-unit($up);
    @media (min-width: $down * 1px) and (max-width: $up * 1px) {
      @content;
    }
}

// Espacement
// ==================
@mixin container($width, $padding) {
  width: 100%;
  padding-left: $padding/4;
  padding-right: $padding/4;

  @include up($width/2) {
    padding-left: $padding;
    padding-right: $padding;
  }

  @include up($width + 2 * $padding) {
    padding-left: calc(50vw - #{$width / 2});
    padding-right: calc(50vw - #{$width / 2});
  }
}


// Typographie
// ==================
@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
    $src: null;
  
    $extmods: (
      eot: '?',
      svg: '#' + str-replace($name, ' ', '_')
    );
  
    $formats: (
      otf: 'opentype',
      ttf: 'truetype'
    );
  
    @each $ext in $exts {
      $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
      $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
      $src: append($src, url(quote($path + '.' + $extmod)) format(quote($format)), comma);
    }
  
    @if length($weight) == 1 {
      @font-face {
        font-family: quote($name);
        font-style: $style;
        font-weight: $weight;
        font-display: swap;
        src: $src;
      }
    } @else {
      $weights: $weight;
      @each $weight in $weights {
        $suffix: map-get(
          (
            200: ExtraLight,
            300: Light,
            400: Regular,
            500: Medium,
            600: SemiBold,
            700: Bold,
            900: Black
          ),
          $weight
        );
        @include font-face($name, $path + '-' + $suffix, $weight, $style, $exts);
      }
    }
} 

@mixin dark () {
    @if (&) {
      @media (prefers-color-scheme: dark) {
        body:not([class~='theme-light']) & {
          @content;
        }
      }
      .theme-dark &{
        @content;
      }
    } @else {
  
      @media (prefers-color-scheme: dark) {
        body:not([class~='theme-light']) {
          @content;
        }
      }
      .theme-dark {
        @content;
      }
  }
}